import {
	regExp
} from '@/common/utils.js'
export default{
	components:{
		
	},
	data(){
		return {
			state:false,
			mobile:'',
			code:'',
			password:'',
			pwd:'',
			timer: null,
			seconds: 60,
		}
	},
	computed: {
		
	},
	onLoad(){
		
	},
	methods:{
		enterPage(){
			uni.navigateTo({
				url:'/pages/login/enterPage'
			})
		},
		//验证码倒计时
		countDown() {
			this.state = true
			this.timer = setInterval(() => {
				this.seconds--
				if (this.seconds <= 0) {
					clearInterval(this.timer)
					this.state = false
					this.seconds = 60
					return
				}
			}, 1000)
		},
		//获取验证码
		getCode() {
			if (!this.state) {
				if (regExp('mobile', this.mobile)) {
					this.countDown()
					this.$http({
						api: 'user/sendcode',
						method: 'POST',
						params: {
							mobile: this.mobile
						}
					}).then(res => {
						// let getNum = res.data.message.replace(/[^0-9]/ig,"")
						// this.nowCode = Number(getNum)
					}).catch(err => {
		
					})
				} else {
					uni.showToast({
						icon: 'none',
						title: '请输入正确的手机号'
					})
				}
			}
		},
		registerBtn(){
			this.judgeFun().then(() => {
				
			}).catch(()=>{})
		},
		judgeFun(){
			return new Promise((resolve,reject) => {
				let tip = ''
				if(!regExp('mobile', this.mobile)){
					tip = '请正确填写您的手机号码'
					reject()
				}else if(!this.code){
					tip = '请正确填写您的验证码'
					reject()
				}else if(!regExp('password',this.password)){
					tip = '请输入6位以上,字母、数字或符号组合的密码'
					reject()
				}else if(this.password !== this.pwd){
					tip = '填写密码不一致'
					reject()
				}else{
					resolve()
				}
				if(tip){
					uni.showToast({
						icon:'none',
						title:tip
					})
				}
			})
		}
	}
}
